.home {
  border: 2px solid black;
  display: block;
  width: 100%;
  height: calc(100vh - 20px);

  .icon {
    display: block;
    width: 100%;
    height: 100px;
  }

  .nav-bar {
    display: inline-block;
    width: 100px;
    height: calc(100% - 100px);
    border: 1px solid orange;

    * {
      border-radius: 20px;
      display: block;
      border: 1px solid pink;
      margin-top: 10px;
      height: 30px;
      cursor: pointer;
    }
  }
}

.show-content {
  display: inline-block;
  position: absolute;
  width: calc(100% - 100px);
  height: calc(100% - 100px);
  border: 1px solid yellow;
}


.scrap-text {
  margin-left: 10px;
}

.place-list {
  display: inline-block;
  width: 200px;
  height: 200px;
  border: 1px solid pink;
}

