.place-list-box {
  margin-left: 20px;
}

.go-list {
  margin-top: 20px;
  width: 100px;
  height: 40px;
  border-radius: 50px;
  border: 1px solid pink;
  cursor: pointer;
}

.place-list {
  margin: 0 20px 20px 0;

  > :first-child {
    height: 150px;
  }

  .x-box {
    position: relative;
    display: inline-block;
    left: 170px;
    top: -165px;
    width: 20px;
    text-align: center;
    border-radius: 50px;
    border: 1px solid pink;
    cursor: pointer;
  }

  .place-name {
    text-align: center;
  }
}