.hidden {
display: none;
}

.set-block {
  > * {
   display: inline-block; 
   margin-bottom: 15px;
  }
  > label {
    margin-right: 10px;
  }
  > input {
    width: 200px;
    height: 20px;
    border: none;
    border-bottom: 1px solid pink;
  }
  
}

.add-place-box {
  > * {
    margin-left: 20px;
  }
  > button {
    margin-top: 20px;
    width: 60px;
    height: 50px;
    border-radius: 50px;
    border: 1px solid pink;
    cursor: pointer;
  }
}
